import React, { useState, useEffect } from 'react'
import {Axios} from '../Shared/Axios'

import Table from './Table'
import Loader from './../UI/Loader'
import {ConvertInt} from './../Shared/ConvertInt'

const Whales = () => {
  const [Stats, setStats] = useState([])
  const [Loadings, setLoading] = useState(true)
  
  useEffect(() => {
    Axios('/api/getData')
    .then(data => {
      console.log(data.WhaleHoldingsOne)
      var temp = [
        {Data: [
          ConvertInt(data.WhaleHoldingsOne),
          ConvertInt(data.WhaleHoldingsTwo),
          ConvertInt(data.WhaleHoldingsThree)
        ]},
        {Data: [
          parseFloat((data.WhaleHoldingsOne / data.Supply)*100).toFixed(2).toString() + ' %',
          parseFloat((data.WhaleHoldingsTwo / data.Supply)*100).toFixed(2).toString() + ' %',
          parseFloat((data.WhaleHoldingsThree / data.Supply)*100).toFixed(2).toString() + ' %'
        ]},
        {Data: [
          <p className={parseFloat(data.WhaleHoldingsOneChangePercent).toFixed(3) >= 0 ? "Positiv__text" : "Negativ__text"}>{parseFloat(data.WhaleHoldingsOneChangePercent).toFixed(3).toString() + ' %'}</p>,
          <p className={parseFloat(data.WhaleHoldingsTwoChangePercent).toFixed(3) >= 0 ? "Positiv__text" : "Negativ__text"}>{parseFloat(data.WhaleHoldingsTwoChangePercent).toFixed(3).toString() + ' %'}</p>,
          <p className={parseFloat(data.WhaleHoldingsThreeChangePercent).toFixed(3) >= 0 ? "Positiv__text" : "Negativ__text"}>{parseFloat(data.WhaleHoldingsThreeChangePercent).toFixed(3).toString() + ' %'}</p>
        ]},
      ]
      setStats(prev => prev = temp)
      setLoading(prev => prev = !prev)
    })
  }, [])

  return (
    <div className="padding-small">
      {Loadings ? 
        <Loader /> :
        <Table 
          TableID="Whales"
          Headers={["Top 10", "Top 25", "Top 100"]}
          TableData={Stats}
          TableStyle="Horizontal"
        />
      }
    </div>
  )
}

export default Whales
