import React, {useState, useEffect} from 'react'

import {Axios} from '../Shared/Axios'

import Table from './TableLink'
import { ConvertInt } from '../Shared/ConvertInt'

const Transactions = (props) => {
    const [isBusy, setBusy] = useState(true)
    const [Buys, setBuys] = useState([])
    const [Sells, setSells] = useState([])


    useEffect(() => {
        Axios('/api/getTransactions')
        .then(response => {
            console.log(response)
            var Sells = []
            for(let Sell of response[1]) {
                Sells.push({Data: [ConvertInt(Sell.Amount), parseInt(Sell.createdAt) + " Min"], Link: "https://bscscan.com/tx/" + Sell.Hash})
            }
            setSells(Sells)
            var Buys = []
            for(let Buy of response[0]) {
                Buys.push({Data: [ConvertInt(Buy.Amount), parseInt(Buy.createdAt) + " Min"], Link: "https://bscscan.com/tx/" + Buy.Hash})
            }
            setBuys(Buys)
            setBusy(false)
        })
    }, [])

    return (
            <div className="big-container padding-small flex flex-standard-wrap">
                
                <div className="half-container padding-small">
                    <h2>Buys</h2>
                    {!isBusy && 
                        <Table
                            TableID="BuyTransactions"
                            Headers={["Amount", "Time"]}
                            TableData={Sells}
                            TableStyle="Horizontal"
                        />
                    }
                </div>
                <div className="half-container padding-small">
                    <h2>Sells</h2>
                    {!isBusy && 
                        <Table
                        TableID="SellTransactions"
                        Headers={["Amount", "Time"]}
                        TableData={Buys}
                        TableStyle="Horizontal"
                        />
                    }
                </div>
            </div>
    )
}

export default Transactions
