import React, {useState, useEffect} from 'react'
import {Axios} from '../Shared/Axios'

import Table from './TableLink'
import { ConvertInt } from '../Shared/ConvertInt'

const AllWhales = () => {
    const [isBusy, setBusy] = useState(true)
    const [Data, setData] = useState([])

    useEffect(() => {
        Axios('/api/getWhales')
        .then(response => {
            var temp = []
            for (let Holder of response){
                var Style
                if(parseInt(Holder.ChangeWeek / 1000) > 2200){
                    Style = 'Positiv'
                } else if (parseInt(Holder.ChangeWeek / 1000) < 0){
                    Style = 'Negativ'
                } else {Style = ""}
                temp.push({Data: 
                    [Holder.num_row, 
                    Holder.Adress,
                    ConvertInt(Holder.Amount), 
                    ConvertInt(Holder.Change), 
                    ConvertInt(Holder.ChangeWeek)], 
                    Link: "/Holders/" + Holder.Adress,
                    NewPage : false,
                    Style: Style
                })
            }
            setData(prev => prev =temp)
            setBusy(false)
        })
    }, [])

    return (
        <div className="u-margin-top-small">
            <h2>
                Top 100 whales
            </h2>
            {!isBusy && <Table 
                TableID="WhalesTable"
                Headers={["#", "Hash", "Balance", "24H", "7D"]}
                TableData={Data}
                TableStyle="Horizontal"
            />}
        </div>
    )
}

export default AllWhales
